.container-fluid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 70px;
  background-color: rgb(80, 77, 77);
  position: fixed;
}
.container-fluid h4 {
  font-size: 25px;
  font-weight: bold;
  align-items: center;
  padding-top: 16px;
  letter-spacing: 1px;
  margin-left: 2rem;
}
.container-fluid h4 a {
  text-decoration: none;
  color: white;
}
.container-fluid h4 a:hover {
  color: yellow;
}

.nav-menu {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.nav-menu ul {
  list-style-type: none;
  color: white;
  align-items: center;
  padding-top: 1rem;
}
.nav-menu ul li {
  display: inline;
  margin-left: 1rem;
  font-size: 18px;
}
.nav-menu ul li:hover {
  cursor: pointer;
  color: yellow;
}
.login button {
  margin: 8px;
  margin-top: 16px;
}
.login .signin {
  background-color: rgb(230, 86, 86) !important;
  border: none;
  color: white;
}
.login .signin:hover {
  background-color: rgb(243, 127, 127) !important;
}
.login .signup {
  background-color: rgb(19, 124, 19);
  border: none;
}
.login .signup:hover {
  background-color: rgb(88, 133, 88) !important;
}

.sigin-form {
  position: absolute;
  width: 40%;
  margin-left: 20rem;
  height: 350px;
  border: 1px solid rgb(238, 230, 230);
  padding-top: 2rem;
  margin-top: 5rem;
  z-index: -999;
  padding-left: 3.2rem;
  box-shadow: 3px 3px 3px rgb(65, 64, 64);
}
.sigin-form .title {
  text-align: center;
  padding-bottom: 2rem;
  font-size: 20px;
  color: rgb(126, 122, 122);
}
.sigin-form .form-control {
  width: 85%;
}
.sigin-form button[type="submit"] {
  margin-top: 1.5rem;
  padding: 5px;
  width: 20%;
}

.sigin-form .form-group {
  display: flex;
}
.sigin-form .already {
  font-size: 11px;
  margin-top: 2rem;
  margin-left: 1rem;
}
.register-form {
  position: absolute;
  width: 40%;
  margin-left: 20rem;
  height: 450px;
  border: 1px solid rgb(238, 230, 230);
  padding-top: 1rem;
  margin-top: 5rem;
  z-index: -999;
  padding-left: 3.2rem;
  box-shadow: 3px 3px 3px rgb(131, 128, 128);
}
.register-form .title {
  text-align: center;
  padding-bottom: 2rem;
  font-size: 20px;
  color: rgb(126, 122, 122);
}
.register-form .form-control {
  width: 85%;
}
.register-form button {
  margin-top: 1.5rem;
  padding: 5px;
  width: 20%;
  background-color: rgb(19, 141, 19);
  border: none;
}
.register-form button:hover {
  background-color: rgb(115, 202, 104);
}

.register-form .form-group {
  display: flex;
}
.register-form .already {
  font-size: 11px;
  margin-top: 2rem;
  margin-left: 1rem;
}
.error {
  color: red;
  font-size: 10px;
  padding-bottom: 0px;
}
